import { LayaEnv } from "../../../LayaEnv";
import { EPhysics2DShape } from "../factory/IPhysics2DFactory";
import { Physics2D } from "../Physics2D";
import { Physics2DShapeBase } from "./Physics2DShapeBase";
export class BoxShape2D extends Physics2DShapeBase {
    get height() {
        return this._height;
    }
    set height(value) {
        if (value < 0)
            console.warn("BoxCollider size cannot be less than 0");
        if (this._height == value)
            return;
        this._height = value;
        this._updateShapeData();
    }
    get width() {
        return this._width;
    }
    set width(value) {
        if (value < 0)
            console.warn("BoxCollider size cannot be less than 0");
        if (this._width == value)
            return;
        this._width = value;
        this._updateShapeData();
    }
    constructor() {
        super();
        this._width = 100;
        this._height = 100;
        this._shapeDef.shapeType = EPhysics2DShape.BoxShape;
    }
    _createShape() {
        this._box2DShape = Physics2D.I._factory.createShape(this._physics2DManager.box2DWorld, this._box2DBody, EPhysics2DShape.BoxShape, this._box2DShapeDef);
        this._updateShapeData();
    }
    _updateShapeData() {
        if (!LayaEnv.isPlaying || !this._body)
            return;
        let helfW = this._width * 0.5;
        let helfH = this._height * 0.5;
        var center = {
            x: helfW + this.pivotoffx,
            y: helfH + this.pivotoffy
        };
        let shape = this._box2DShape ? Physics2D.I._factory.getShape(this._box2DShape, this._shapeDef.shapeType) : Physics2D.I._factory.getShapeByDef(this._box2DShapeDef, this._shapeDef.shapeType);
        Physics2D.I._factory.set_collider_SetAsBox(shape, helfW, helfH, center, Math.abs(this.scaleX), Math.abs(this.scaleY));
    }
    clone() {
        let dest = new BoxShape2D();
        this.cloneTo(dest);
        return dest;
    }
    cloneTo(destObject) {
        super.cloneTo(destObject);
        destObject.width = this.width;
        destObject.height = this.height;
    }
}
