import { LayaEnv } from "../../../LayaEnv";
import { EPhysics2DShape } from "../factory/IPhysics2DFactory";
import { Physics2D } from "../Physics2D";
import { Physics2DShapeBase } from "./Physics2DShapeBase";
export class PolygonShape2D extends Physics2DShapeBase {
    get datas() {
        return this._datas;
    }
    set datas(value) {
        if (!value)
            throw "PolygonCollider points cannot be empty";
        this._datas = value;
        this._updateShapeData();
    }
    constructor() {
        super();
        this._datas = [50, 0, 100, 100, 0, 100];
        this._shapeDef.shapeType = EPhysics2DShape.PolygonShape;
    }
    _createShape() {
        this._box2DShape = Physics2D.I._factory.createShape(this._physics2DManager.box2DWorld, this._box2DBody, EPhysics2DShape.PolygonShape, this._box2DShapeDef);
        this._updateShapeData();
    }
    _updateShapeData() {
        if (!LayaEnv.isPlaying || !this._body)
            return;
        var len = this.datas.length;
        if (len < 6)
            throw "PolygonCollider points must be greater than 3";
        if (len % 2 == 1)
            throw "PolygonCollider points lenth must a multiplier of 2";
        let shape = this._box2DShape ? Physics2D.I._factory.getShape(this._box2DShape, this._shapeDef.shapeType) : Physics2D.I._factory.getShapeByDef(this._box2DShapeDef, this._shapeDef.shapeType);
        Physics2D.I._factory.set_PolygonShape_data(shape, this.pivotoffx, this.pivotoffy, this.datas, this.scaleX, this.scaleY);
    }
    clone() {
        let dest = new PolygonShape2D();
        this.cloneTo(dest);
        return dest;
    }
    cloneTo(destObject) {
        super.cloneTo(destObject);
        destObject.datas = this.datas;
    }
}
