import { LayaEnv } from "../../../LayaEnv";
import { EPhysics2DShape } from "../factory/IPhysics2DFactory";
import { Physics2D } from "../Physics2D";
import { Physics2DShapeBase } from "./Physics2DShapeBase";
export class ChainShape2D extends Physics2DShapeBase {
    get datas() {
        return this._datas;
    }
    set datas(value) {
        if (!value)
            throw "ChainCollider datas cannot be empty";
        this._datas = value;
        this._updateShapeData();
    }
    get loop() {
        return this._loop;
    }
    set loop(value) {
        if (this._loop == value)
            return;
        this._loop = value;
        this._updateShapeData();
    }
    constructor() {
        super();
        this._datas = [0, 0, 100, 0];
        this._loop = false;
        this._shapeDef.shapeType = EPhysics2DShape.ChainShape;
    }
    _createShape() {
        this._box2DShape = Physics2D.I._factory.createShape(this._physics2DManager.box2DWorld, this._box2DBody, EPhysics2DShape.ChainShape, this._box2DShapeDef);
        this._updateShapeData();
    }
    _updateShapeData() {
        if (!LayaEnv.isPlaying || !this._body)
            return;
        var len = this._datas.length;
        if (len % 2 == 1)
            throw "ChainCollider datas lenth must a multiplier of 2";
        let shape = this._box2DShape ? Physics2D.I._factory.getShape(this._box2DShape, this._shapeDef.shapeType) : Physics2D.I._factory.getShapeByDef(this._box2DShapeDef, this._shapeDef.shapeType);
        Physics2D.I._factory.set_ChainShape_data(shape, this.pivotoffx, this.pivotoffy, this._datas, this._loop, this.scaleX, this.scaleY);
    }
    clone() {
        let dest = new ChainShape2D();
        this.cloneTo(dest);
        return dest;
    }
    cloneTo(destObject) {
        super.cloneTo(destObject);
        destObject.datas = this.datas;
        destObject.loop = this.loop;
    }
}
